import Ls from './ls'
import axios from 'axios';

export default {
    async login(loginData) {
        try {
            let response = await axios.post('/api/auth/login', loginData);
            console.log(response);
            Ls.set('auth.token', response.data.token);
            toastr['success']('ng nhp thnh cng!', 'Thng ip');
            return response.data.token
        } catch (error) {
            if (error.response.status === 401) {
                window.toastr['error']('a ch email hoc mt khu khng ng!', 'Li');
            } else {
                console.log('Error', error.message);
            }
        }
    },

    async logout() {
        try {
            await axios.get('/api/auth/logout');

            Ls.remove('auth.token');
            toastr['success']('ng xut thnh cng!', 'Thng ip');
        } catch (error) {
            console.log('Error', error.message);
        }
    },

    async check() {
        let response = await axios.get('/api/auth/check');

        return !!response.data.authenticated;
    },
}
